import { DxDropDownBase } from "../dropdownbase/dx-dropdown-base3";
import { DateEditRootKbdStrategy } from "./keyboard-navigation/date-edit-drop-down-kbd-strategy";
export class DxDateEditBase extends DxDropDownBase {
    connectedCallback() {
        super.connectedCallback();
    }
    disconnectedCallback() {
        super.disconnectedCallback();
    }
    toggleDropDownVisibility() {
        if (this.fieldText !== this.fieldElementValue)
            this.raiseFieldChange();
        super.toggleDropDownVisibility();
    }
    openDropDown() {
        if (this.fieldText !== this.fieldElementValue)
            this.raiseFieldChange();
        super.openDropDown();
    }
    createKeyboardStrategy() {
        return new DateEditRootKbdStrategy(this);
    }
}
